<script lang="ts">
  import { onMount } from 'svelte';
  import { auth } from '../store/auth';
  import { get } from 'svelte/store';
  import { showError } from '../store/notifications';
  import MemoryManager from './MemoryManager.svelte';

  export let passportId: bigint;
  
  let passport: any = null;
  let loading = true;
  let showMemories = false;

  onMount(() => {
    // Load passport when component mounts if auth is ready
    const $auth = get(auth);
    if ($auth && $auth.state === 'initialized') {
      loadPassport();
    } else {
      // Wait for auth to be initialized
      const unsubscribe = auth.subscribe(($auth) => {
        if ($auth && $auth.state === 'initialized') {
          loadPassport();
          unsubscribe();
        }
      });
    }
  });

  async function loadPassport() {
    loading = true;
    try {
      const $auth = get(auth);
      console.log('🔍 PassportViewer - Loading passport, auth state:', {
        state: $auth?.state,
        hasActor: !!$auth?.actor,
        hasClient: !!$auth?.client,
        passportId: String(passportId)
      });
      
      if (!$auth || $auth.state !== 'initialized') {
        console.error('🚨 PassportViewer - Authentication not initialized:', $auth?.state);
        showError('Authentication required');
        return;
      }

      const result = await $auth.actor.get_agent_passport(passportId);
      if (result.length > 0) {
        passport = result[0];
        
        // Decrypt specifications if they exist
        if (passport.encrypted_specifications) {
          try {
            passport.decrypted_specifications = await $auth.crypto.decryptWithNoteKey(
              passportId,
              passport.owner,
              passport.encrypted_specifications
            );
          } catch (error) {
            console.warn('Could not decrypt specifications:', error);
            passport.decrypted_specifications = '[Encrypted]';
          }
        }
      }
    } catch (error) {
      showError(`Failed to load passport: ${error.message}`);
    } finally {
      loading = false;
    }
  }

  function formatTimestamp(timestamp: bigint): string {
    const date = new Date(Number(timestamp) / 1_000_000);
    return date.toLocaleString();
  }

  function getStatusBadge(isActive: boolean): string {
    return isActive ? 'badge-success' : 'badge-error';
  }
</script>

{#if loading}
  <div class="flex justify-center items-center h-64">
    <span class="loading loading-spinner loading-lg"></span>
  </div>
{:else if passport}
  <div class="card bg-base-100 shadow-xl">
    <div class="card-body">
      <div class="flex justify-between items-start">
        <h2 class="card-title">
          {passport.agent_name}
          <div class="badge {getStatusBadge(passport.is_active)}">
            {passport.is_active ? 'Active' : 'Inactive'}
          </div>
        </h2>
        <div class="badge badge-outline">{passport.agent_type}</div>
      </div>

      <div class="grid grid-cols-1 md:grid-cols-2 gap-4 mt-4">
        <div>
          <h3 class="font-semibold text-sm opacity-70">Passport ID</h3>
          <p class="font-mono text-sm">{passport.id}</p>
        </div>
        
        <div>
          <h3 class="font-semibold text-sm opacity-70">Owner</h3>
          <p class="font-mono text-xs break-all">{passport.owner}</p>
        </div>

        <div>
          <h3 class="font-semibold text-sm opacity-70">Created</h3>
          <p class="text-sm">{formatTimestamp(passport.created_at)}</p>
        </div>

        <div>
          <h3 class="font-semibold text-sm opacity-70">Last Active</h3>
          <p class="text-sm">{formatTimestamp(passport.last_active)}</p>
        </div>
      </div>

      <div class="mt-4">
        <h3 class="font-semibold text-sm opacity-70 mb-2">Capabilities</h3>
        <div class="flex flex-wrap gap-2">
          {#each passport.capabilities as capability}
            <div class="badge badge-primary">{capability}</div>
          {/each}
        </div>
      </div>

      {#if passport.api_endpoints && passport.api_endpoints.length > 0}
        <div class="mt-4">
          <h3 class="font-semibold text-sm opacity-70 mb-2">API Endpoints</h3>
          <div class="flex flex-wrap gap-2">
            {#each passport.api_endpoints as endpoint}
              <div class="badge badge-secondary">{endpoint}</div>
            {/each}
          </div>
        </div>
      {/if}

      {#if passport.decrypted_specifications}
        <div class="mt-4">
          <h3 class="font-semibold text-sm opacity-70 mb-2">Specifications</h3>
          <div class="bg-base-200 p-3 rounded-lg">
            <pre class="text-sm whitespace-pre-wrap">{passport.decrypted_specifications}</pre>
          </div>
        </div>
      {/if}

      <div class="card-actions justify-end mt-6">
        <button
          class="btn btn-outline"
          on:click={() => showMemories = !showMemories}
        >
          {showMemories ? 'Hide' : 'Show'} Memories
        </button>
        <button class="btn btn-primary" on:click={loadPassport}>
          Refresh
        </button>
      </div>
    </div>
  </div>

  {#if showMemories}
    <div class="mt-6">
      <MemoryManager {passportId} />
    </div>
  {/if}
{:else}
  <div class="alert alert-error">
    <span>Passport not found or access denied</span>
  </div>
{/if}
